<template>
	<div class="order-info-block fx-shadow-block">
		<van-collapse v-model="activeNames" :border="false">
			<van-collapse-item name="1" :disabled="collapseDisabled">
				<div slot="title" class="info-title">
					<span class="info-title-name"></span>
					<stateText class="stateTag fl-r" :billState="data.billState"></stateText>
					<span class="info-title-weightState" v-show="data.weightState === 1"></span>
				</div>
				<van-tabs v-model="tabActive" animated>
					<van-tab title="">
						<van-cell
							class="info-item"
							title=""
							value-class="info-item-value"
							:value="data.shopDTO && data.shopDTO.name"
							:is-link="!isDisabled && data.autoType === 1"
							@click="onEmitClick('on-shop-click')"
						/>
						<van-cell
							class="info-item"
							title=""
							value-class="info-item-value"
							:value="data.houseDTO && data.houseDTO.name"
							:is-link="!isDisabled && data.autoType === 1"
							@click="onEmitClick('on-house-click')"
						/>
						<van-cell
							class="info-item"
							title=""
							value-class="info-item-value"
							:value="data.customerDTO && data.customerDTO.name"
							:is-link="!isDisabled && data.autoType === 1"
							@click="onEmitClick('on-customer-click')"
						/>
						<van-cell
							class="info-item"
							title=""
							:value="formatterDate(data.businessDate)"
							:is-link="!isDisabled"
							@click="onBusDateClick"
						/>
						<van-cell
							class="info-item"
							title=""
							:value="formatterDate(data.arriveDate)"
							:is-link="!isDisabled && data.autoType !== 10"
							@click="onEmitClick('on-arriveDate-click')"
						/>
						<van-cell
							class="info-item"
							title=""
							value-class="info-item-value"
							:value="data.businessTypeDTO && data.businessTypeDTO.name"
							@click="onEmitClick('on-billType-click')"
						>
							<van-icon
								slot="right-icon"
								v-show="!isDisabled && data.autoType === 1"
								:name="!hasBillType ? 'arrow' : 'close'"
								class="item-clear-btn"
								@click.stop="!hasBillType ? onEmitClick('on-billType-click') : onEmitClick('on-billType-clear')"
							/>
						</van-cell>
						<van-cell class="info-item" title="" :value="formatterStockType(data.stockType)" />
						<van-cell v-if="isAutoType([10])" class="info-item" title="" :value="data.thirdPartyId" />
						<van-cell v-if="isAutoType([10])" class="info-item" title="" :value="formatterPaymentWay(data.paymentWay)" />
					</van-tab>
					<van-tab title="">
						<van-cell class="info-item" title="" :value="data.billEmp" />
						<van-cell class="info-item" title="" :value="formatterDate(data.billDate)" />
						<van-cell class="info-item" title="" :value="data.checkEmp" />
						<van-cell class="info-item" title="" :value="formatterDate(data.checkDate)" />
						<van-pro-field
							v-model="data.manualCode"
							class="info-item"
							label=""
							:placeholder="isDisabled ? '' : ''"
							input-align="right"
							maxlength="50"
							:disabled="isDisabled || isAutoType([10])"
						/>
						<van-pro-field
							v-model="data.billMemo"
							class="info-item"
							label=""
							:placeholder="isDisabled ? '' : ''"
							input-align="right"
							maxlength="50"
							:disabled="isDisabled || isAutoType([10])"
						/>
						<!-- <van-cell class="info-item" title="" :value="data.highBill" is-link /> -->
						<!-- <van-cell class="info-item" title="" :value="data.lowBill" is-link /> -->
						<!-- <van-cell class="info-item" title="" :value="data.billRecord" is-link /> -->
						<van-cell v-if="isAutoType([10])" class="info-item" title="" :value="data.orderUser" />
						<van-cell v-if="isAutoType([10])" class="info-item" title="" :value="data.orderPhone" />
						<van-cell v-if="isAutoType([10])" class="info-item" title="" :value="data.orderAddress" />
					</van-tab>
				</van-tabs>
			</van-collapse-item>
		</van-collapse>
	</div>
</template>

<script>
import { mapState } from 'vuex'
import stateText from '@/components/common/StateText.vue'
export default {
	name: 'orderInfo',
	components: {
		stateText
	},
	props: {
		data: {
			type: Object,
			default: () => {
				return {}
			}
		},
		isDisabled: Boolean,
		collapseDisabled: Boolean
	},
	data () {
		return {
			activeNames: ['1'],
			tabActive: 0
		}
	},
	computed: {
		...mapState({
			sysPointSize: 'sysPointSize',
			numberLength: 'numberLength',
			maxAmount: 'maxAmount'
		}),
		formatterDate (date) {
			return (date) => {
				if (typeof date === 'number') {
					return this.$fxUtils.setDateTime(new Date(date), '')
				} else {
					return ''
				}
			}
		},
		formatterStockType (data) {
			return (date) => {
				if (date === 3) {
					return ''
				} else if (date === 2) {
					return ''
				} else {
					return ''
				}
			}
		},
		formatterPaymentWay () {
			return (date) => {
				if (date === 0) {
					return ''
				} else if (date === 1) {
					return ''
				} else if (date === 2) {
					return ''
				} else if (date === 3) {
					return ''
				} else if (date === 4) {
					return ''
				} else {
					return ''
				}
			}
		},
		hasBillType () {
			return this.data.businessTypeDTO && this.data.businessTypeDTO.id
		},
		//  0 1 102.0
		// 2.0
		isAutoType (state) {
			return state => {
				if (Object.prototype.toString.call(state) === '[object Array]') {
					return state.includes(this.data.autoType)
				} else {
					return this.data.autoType === state
				}
			}
		}
	},
	methods: {
		onEmitClick (type) {
			if (this.isDisabled || (this.data.autoType !== 1 && this.data.autoType !== undefined)) {
				return false
			}
			this.$emit(type)
		},
		onBusDateClick () {
			if (this.isDisabled || this.isAutoType([0])) {
				return false
			}
			this.$emit('on-busdate-click')
		},
		closeCollapse () {
			this.activeNames = []
		}
	}
}
</script>
<style lang="stylus" scoped>
@import '~$assets/stylus/varsty.styl'
.order-info-block {
	width:340px
	margin:10px auto
	padding:5px
	text-align:left
	.info-title {
		.info-title-name {
			font-size:12px
		}
		.stateTag {
			font-size:12px
			margin-right:10px
		}
		.info-title-weightState {
			margin-left:20px
			color:$fxRed
			font-size:16px
		}
	}
	.info-item {
		font-size:12px !important
		padding:5px 0 !important
		.info-item-value {
			text-overflow: ellipsis;
			white-space: nowrap;
		}
		.item-clear-btn {
			width:16px
			height:24px
			line-height:24px
			margin-left:5px
			font-size:16px
			color: $fxGraylight
		}
	}
}
</style>

